          :key="item"
          src="/static/images/star-fill.png"
          mode="scaleToFill"
          class="star-image"
        />
      </view>
      <view class="row2">
        <paTag v-for="(sport_item, sindex) in tagList" :key="sindex" class="sport_item">
          {{ sport_item }}
        </paTag>
      </view>
    </view>
  </view>
</template>

<script>
import defaultAvatar from '@/static/images/avatar.png'
export default {
  name: 'stadiumItem',
  emits: ['reserve', 'click'],
  props: {
    info: {
      type: Object,
      default: () => {
        return {}
      },
    },
    pageType: {
      type: String,
      default: () => {
        return 'level'
      },
    },
    type: {
      type: String,
      default: () => {
        return ''
      },
    },
  },
  data() {
    return {}
  },
  computed: {
    toDistance() {
      let item = this.info
      let distance = '位置未知'
      return distance
    },
    tagList() {
      return this.info.tag_ids_arr ? this.info.tag_ids_arr.map((e) => e.tag_id_str) : []
    },
    showImage() {
      return this.info.avatar_str ? this.info.avatar_str : defaultAvatar
    },
    showStar() {
      return this.pageType == 'star' && this.info.star_level > 0
    },
  },
  methods: {
    getIcon(type, value) {
      return this.$dict.getDictLabel('promoteLevelList', value, {
        labelKey: type,
      })
    },
    handleReserve() {
      this.$emit('reserve', this.info)
    },
    toDetail() {
      this.$emit('click', this.info)
      this.$emit('toDetail', this.info)
    },
  },
}
</script>

